.nav-container{
    margin: 0px auto;
    min-width: 100%;
    position: absolute;
}

.nav-options{
    margin: auto;
    display:flex;
    justify-content: center;
    width: 60%;
    background: rgb(227, 236, 227);
    padding: 10px;
    border-radius: 0px 0px 10px 10px;
  
}

.input-option{
    padding: 0px 10px;
    background: rgb(119, 165, 119);
    margin: 5px;
    height: 40px;
    width: 120px;
    border-radius: 5px;
    transition: all 0.3s;
}

.back{
    background: rgb(162, 168, 162);
}

.input-option:hover{
    background: rgb(148, 182, 148);
}

.nav-options a{
    text-decoration: none;
}

.input-option p{
    
    color: white;
    line-height: 40px;
    font-size: 14px;
}

/*---------SELECTED OPTION----------*/

