.calendar{
    margin: auto;
}

.name-days{
    margin: auto;
    padding: 0%;
    width: 95%;
    column-gap: 3px;
}

.calendar .name-days .day{
    width: 13.5%;
    height: 25px;
    border-radius: 3px;
    background-color: rgb(80, 133, 80);
    color: white;
    font-size: 13px;
    text-align: left;
    line-height: 25px;
}

.days{
    margin: auto;
    padding: 0%;
    width: 95%;
    column-gap: 3px;
}

.days .day{
    width: 13.5%;
    height: 25px;
    border-radius: 3px;
    background-color: rgb(224, 240, 224);
    color: rgb(84, 138, 117);
    font-size: 13px;
    text-align: left;
    line-height: 25px;
    margin-top: 5px;
}

.day p{
    width: 18px;
}

