.logo{
    width: 120px;
    margin: 5px;
    margin-left: 20px;
}

.navbar{
    box-shadow: 0px 0px 20px rgb(175, 170, 170);
    z-index: 10;
}

.container-nav{
    display: flex;
    margin: auto;
    width: 50%;
}

.mr-auto a i{
    color:  #00A859;
    margin-right: 10px;
}

.mr-auto a{
    text-decoration: none;
    color:black;
    margin-left: 30px;
}


@media (max-width: 767.98px) {
    
    .container-nav{
        width: 100%;
        display: block;
    }
    
    .mr-auto{
        
        text-align: center;
    }

    .mr-auto a{
        border-radius: 10px;
        margin: 10px 20px;
        background-color: #45af7e;
        padding: 10px;
        color: white;
        box-shadow: 3px 3px 5px rgb(207, 204, 204);

    }

    .mr-auto a i{
       color:  #c5f1dd;
    }
   
    .navbar{
        padding-right: 20px;
        padding-left: 20px;
    }
  }

