/*-------------------GOBACK--------------------------*/
.goback{
    display:none
}

@media (max-width: 991px) {
        
    .goback{
        display:block;
        background: linear-gradient(#00A859, #6f7c6f);    
        width: 50px;
        height: 50px;
        border-radius: 100px;
        position: fixed;
        bottom: 3%;
        left: 82%;
        box-shadow: 2px 2px 10px rgb(158, 157, 157);
    }
    
    .goback i{
        position: relative;
        color: white;
        font-size: 25px;
        top: 7px;
    }
  }


