    }
    
    .mr-auto{
        
        text-align: center;
    }

    .mr-auto a{
        border-radius: 10px;
        margin: 10px 20px;
        background-color: #45af7e;
        padding: 10px;
        color: white;
        box-shadow: 3px 3px 5px rgb(207, 204, 204);

    }

    .mr-auto a i{
       color:  #c5f1dd;
    }
   
    .navbar{
        padding-right: 20px;
