/*-------------------GOBACK--------------------------*/
.goback{
    display:none
}

@media (max-width: 991px) {
        
    .goback{
        display:block;
        background: linear-gradient(#00A859, #6f7c6f);    
        width: 50px;
        height: 50px;
        border-radius: 100px;
        position: fixed;
        bottom: 3%;
        left: 82%;
        box-shadow: 2px 2px 10px rgb(158, 157, 157);
    }
    
    .goback i{
        position: relative;
        color: white;
        font-size: 25px;
        top: 7px;
    }
  }



/*--------------DEPARTMENTS------------------------*/

.departments{
    margin: auto;
    width: 70%;
    position: absolute;
    top: 9.35em;
    padding: 0px 10px;
    left: 0;
    right: 0;
}

.input-name{
    margin: auto;
    background-color: rgb(235, 241, 235);
    background: linear-gradient(rgb(199, 209, 199), rgb(227, 240, 227));
    width: 75%;
    
    border-radius: 0px 0px 10px 10px;
    padding: 7px 15px;
    z-index: -1;
    color:rgb(32, 122, 103);
    font-weight: bold;
    transition: all 0.3s;
}

.input-bar .button{
    font-size: 1.4em;
}

.input-bar{
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-between;
}

.departments .new-entry{
    background-color: azure;
    font-size: 0.7em;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);
}

.departments .new-entry:hover{
    color:darkgreen
}


.departments .table{
    margin: auto;
    margin-top: 30px;
    width: 70%;
    vertical-align: middle;
}

.departments tbody .td-name{
    text-align: left;
    font-size: 12px;
}

.departments thead .th-name{
    text-align: left;
}

.departments tbody .td-option{
    text-align: right;

}
.departments tbody .td-option button{
    margin-left: 5px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);

}

.departments thead .th-option{
    text-align: right;

}

/*---------------------DEPARTEMENT FORM-------------------------*/

.department-form{
    background-color: #faf6f6;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 10px;
    margin-top: 20px;
    transition: all 0.3s;
}

.department-form p{
    color:black;
    text-shadow: none;
}

.department-form input{
    width: 90%;
}

.department-form button{
    background-color: white;
    margin-left: 10px;
    margin-top: -5px;
}


