.datainput{
    margin-top: 20px;
    position: absolute;
    top: 80px;
    padding: 0px 10px;
    left: 0;
    right: 0;
}

input{
    border: 1px solid rgb(192, 200, 209);
    border-radius: 3px;
}

.datainput a{
    text-decoration: none;
}

.datainput h3{
    color:  #00A859;
    margin-top: 20px;
    margin-bottom: 50px;
}

.datainput span{
    color: rgb(255, 255, 255);
    font-size: 1.1em;
    transition: all 0.3s;
}

.button-title{
    display: flex;
    justify-content: center;
    align-items: center;
}

.datainput i{
    font-size: 1em;
    margin-right: 20px;
    color: #b3dfb3;
    transition: all 0.3s;
}

.options{
    margin: 0px auto;
    display: flex;
    width: 50%;
}

.input-button{
    background: rgb(119, 165, 119);
    height: 4.0em;
    border-radius: 10px;
    cursor: pointer;
    padding: 5px;
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    margin-bottom: 25px;
    box-shadow: 2px 2px 5px rgb(224, 222, 222);
    transition: all 0.3s;
 
}

.input-button:hover{
    background: #b3dfb3;    
}

.input-button:hover i, .input-button:hover span{
    color: darkcyan;
}

.input-button a{
    text-decoration: none;
    color: black;
}


@media (max-width: 991px) {
        
    .datainput{
        margin-top: 20px;
        margin-top: 20px;
    }
    
    .datainput h3{
        color:  #00A859;
        margin-bottom: 20px;
    }
    
    .options{
        margin: 0px auto;
        width: 100%;
        display: flex
    }

    .datainput span{
        color: rgb(255, 255, 255);
        font-size: 16px;
    }

    .datainput i{
        display:none;
    }
    
    .input-button{
        background: rgb(119, 165, 119);
        height: 100px;
        border-radius: 10px;
        cursor: pointer;
        padding: 10px;
        display: flex;
        justify-content: center;
        align-content: center;
        flex-direction: column;
        margin-bottom: 15px;
        box-shadow: 2px 2px 5px rgb(224, 222, 222);
        transition: all 0.3s;
    }

    .input-button:active{
        background: #d2d8d2; 
        box-shadow: 0px 0px 0px rgb(202, 200, 200), 0px 0px 5px rgb(202, 200, 200) inset;
    }
    
    .input-button a{
        text-decoration: none;
        color: black;
    }
    
    .input-button h3{
        margin: 0px auto;
        position: relative;
        float: right;
    }
  }

