/*--------------SERVICE------------------------*/

.personal{
    margin: auto;
    width: 70%;
    position: absolute;
    top: 9.35em;
    padding: 0px 10px;
    left: 0;
    right: 0;
}

.not-list{
    margin: auto;
    margin-top: 20px;
    font-size: 1.2em;
    padding: 0.7em;
    background-color: rgb(224, 245, 224);
    width: 50%;
    border-radius: 10px;
    color: rgb(133, 197, 133);
}

.input-name{
    margin: auto;
    background-color: rgb(235, 241, 235);
    background: linear-gradient(rgb(199, 209, 199), rgb(227, 240, 227));
    width: 75%;
    min-height: 3em;
    border-radius: 0px 0px 10px 10px;
    padding: 7px 15px;
    z-index: -1;
    color:rgb(32, 122, 103);
    font-weight: bold;
    transition: all 0.3s;
}

.input-bar .button{
    font-size: 1.4em;
}

.input-bar select{
    width: 30%;
}

.input-bar{
    display: flex;
    justify-content: center;
    align-items: center;
    justify-content: space-between;
}

.personal .new-entry{
    background-color: azure;
    font-size: 0.7em;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);
}

.personal .new-entry:hover{
    color:darkgreen
}


.personal .table{
    margin: auto;
    margin-top: 30px;
    width: 70%;
    vertical-align: middle;
}

.personal tbody .td-name{
    text-align: left;
    font-size: 12px;
}

.personal thead .th-name{
    text-align: left;
}

.personal tbody .td-option{
    text-align: right;

}
.personal tbody .td-option button{
    margin-left: 5px;
    box-shadow: 2px 2px 5px rgb(197, 195, 195);

}

.personal thead .th-option{
    text-align: right;

}

/*---------------------SERVICE FORM-------------------------*/

.personal-form{
    background-color: #faf6f6;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 10px;
    margin-top: 20px;
    transition: all 0.3s;
    
}

.personal-form p{
    color:black;
    text-shadow: none;
}

.personal-form input{
    width: 45%;
    margin-bottom: 10px;
    margin-left: 15px;
    
    
}

.selected{
    width: 92%;
    margin: auto;
    margin-left: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    justify-content: space-between;
}

.selected select{
    width: 90%;
    margin-right: 20px;
    margin-top: 10px;
}

.personal-form button{
    background-color: white;
    margin-left: 10px;
    margin-top: 10px;
    
}


@media (max-width: 991px) {

    /*--------------SERVICE------------------------*/

    .personal{
        width: 100%;
        top: 4em;
    }

    .input-name{
        min-height: 3.5em;
        width: 100%;
    }

        
    .input-name button{
        margin-top: 10px;
    }

    .input-name span{
        margin-top: 5px;
    }

    .personal .new-entry{
         box-shadow: 2px 2px 5px rgb(197, 195, 195);
    }

    .personal .new-entry p{
        display: none;
    }

    .personal .table{
        margin: auto;
        margin-top: 10px;
        width: 90%;
        vertical-align: middle;
    }

    .personal tbody .td-name{
        text-align: left;
        font-size: 12px;
    }

    .personal thead .th-name{
        text-align: left;
    }

    .personal tbody .td-option{
        text-align: right;

    }
    .personal tbody .td-option button{
        margin-left: 5px;
        box-shadow: 2px 2px 5px rgb(197, 195, 195);

    }

    .personal thead .th-option{
        text-align: right;

    }

    .selected{
        width: 45%;
    }
    
    .selected select{
        width: 100%;
        margin: 5px;
        margin-top: 5px;
        font-size: 0.7em;
        height: 2em;
    }
    

  

    /*---------------------SERVICE FORM-------------------------*/

    .personal-form{
        background-color: #EFEFEF;
        width: 100%;
        display: inline-block;
        padding: 15px;
        border-radius: 10px;
        margin-bottom: 10px;
        
    }

    .personal-form input{
        width: 95%;
    }

    .personal-form button{
        float: right;
        margin-right: 10px;
        margin-top: 10px;
        background-color: white;
    }
}

